/* width */
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.todo {
  margin: auto;
  margin-top: 3vh;
  background: white;
  width: 310px;
  height: 94vh;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 2px 5px 20px rgba(0,0,0,0.5);
  overflow: auto;
}

@media screen and (max-width: 576px) {
  .todo {
    margin-top: 2.5vh;
    width: 95vw;
    height: 95vh;
  }
}
@charset "utf-8";

/* font-family: 'Josefin Sans', sans-serif; */

* {
    font-family: 'Josefin Sans', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* HEADER */
h1 {
    text-align: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    padding: 22px 15px 15px 15px;
    color: white;
    background: #9575cd;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    text-shadow: 2px 3px 5px rgba(0,0,0,0.4);
    letter-spacing: 2px;
}

h1 i {
    font-size: 0.8em !important;
    float: right;
}

h1 i:hover {
    cursor: pointer;
    color: #5d4037;
}

/* FORM */
.taskfield {
    display: grid;
    margin-top: 35px;
    grid-template-columns: 75% 15%;
    grid-gap: 10px;
    margin-left: 25px;
}

input {
    text-align: center;
    border: none;
    border-bottom: 2px solid #d1c4e9;
    color: grey;
    font-size: 1.3em;
    transition: 0.5s;
}

button {
    border-radius: 150px;
    padding: 11px 3px 10px 3px;
    background: #9575cd;
    font-size: larger;
    color: white;
    border: none;
    transition: 0.5s;
    font-weight: bold;
}

input:focus, input:active, button:focus, button:active {
    outline: none;
}

button:hover {
    cursor: pointer;
    background-color: #5cb85c;
}

input:focus {
    border-width: 2.5px;
    border-color: #9575cd;
}

/* TODO LIST */
ol {
    padding: 5% 10%;
    list-style-type: none;
}

ol li {
    color: #9575cd;
    font-size: 1.2em;
    font-weight: 600;
    text-transform: capitalize;
    margin: 25px 0;
    line-height: 1.3;
    overflow: hidden;
    text-overflow: ellipsis;
}

ol i {
    float: right;
    transition: 0.5s;
}

ol i:hover {
    color: #d9534f;
    cursor: pointer;
}
@charset "utf-8";

/* font-family: 'Josefin Sans', sans-serif; */

body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Josefin Sans', sans-serif;
  background: #006064;
}
